import React from 'react'
import { useSelector } from 'react-redux'
import { LineChart, SignIn, Statistics, UsersInviteds } from '../../components'
import { BarChart } from '../../components/Chart/BarChart/BarChart'
import { RootState } from '../../store/reducers/rootReducer'


import './Main.scss'
export const Main = () => {
    const { username } = useSelector(({ user }: RootState) => user)

    if (username === null) {
        return <SignIn />
    }

    return (
        <div className='Main'>
            {username === 'admin' ? <UsersInviteds />
                : <>
                    <Statistics />
                    <div className="Main__charts">
                        <LineChart />
                        <BarChart />
                    </div>
                </>}

        </div>
    )
}
