import React, { useState } from 'react'
import { useDispatch } from 'react-redux'
import './SignIn.scss'


export const SignIn = () => {
    const dispatch = useDispatch()
    const [name, setName] = useState('')

    const handleChange: React.ChangeEventHandler<HTMLInputElement> = (e) => {
        setName(e.target.value.toLowerCase())
    }
    const handleSubmit: React.FormEventHandler = (e) => {
        e.preventDefault()
        dispatch({type: 'USER_SET_ADMIN', payload: name})
    }
    return (
        <div className='SignIn'>
            <form className="SignIn__form" onSubmit={(e) => handleSubmit(e)}>
                <input placeholder='Введите ваше имя' type="text" name="name" value={name} onChange={(e) => handleChange(e)}/>
                <input placeholder='Введите ваш e-mail ' type="text" name="email" id=""/>
                <button type="submit">Войти</button>
            </form>
            <div className="SignIn__youtube">
                <span className="SignIn__youtube-span">Войти с помощью</span>
                <div className="SignIn__youtube-btn">YouTube</div>
            </div>
        </div>
    )
}
