import React, { RefCallback, useEffect } from 'react'
import { Header } from './components'
import { Switch, Route, useLocation } from 'react-router-dom'
import { Account, Cpa, Main, Payouts } from './pages'
import { useSwipeable } from 'react-swipeable'
import { useHistory } from 'react-router-dom'

export const App = () => {
    
    const pathUrl = ['/', '/cpa', '/payouts', '/account']
    const history = useHistory()
    const location = useLocation()

    const { ref } = useSwipeable({
        onSwipedLeft: () => {
            if (pathUrl[pathUrl.length - 1] === location.pathname) {
                history.push(pathUrl[0])
            }
            history.push(pathUrl[pathUrl.indexOf(location.pathname) + 1])
        },
        onSwipedRight: () => {
            if (pathUrl[0] === location.pathname) {
                history.push(pathUrl[pathUrl.length - 1])
            }
            history.push(pathUrl[pathUrl.indexOf(location.pathname) - 1])
        }
    }) as { ref: RefCallback<Document> }

    useEffect(() => {
        ref(document)
    })
    return (
        <>
            <Header />
            <div className="wrapper">
                <Switch>
                    <Route path='/' exact component={Main} />
                    <Route path='/payouts' component={Payouts} />
                    <Route path='/cpa' component={Cpa} />
                    <Route path='/account' component={Account} />
                </Switch>
            </div>
        </>
    )
}
